import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {DatePicker} from "@material-ui/pickers";
import {fieldValidator} from "./validator";

const Step4 = props => {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});
    const [enableNext, setEnableNext] = useState(false);

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'forwardMailStartDate':
                updatedState[fieldName] = value;
                setEnableNext(true);
                setErrorMsg(fieldValidator(fieldName, updatedState, 'required', errors).error);
                break;
            case 'stopForwardingMail':
                updatedState[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'required', errors).error);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {...errorMsg};

        let newForm = fieldValidator('forwardMailStartDate', state, 'required', errors);

        if(state.moveType === "temp"){
            newForm = fieldValidator('stopForwardingMail', state, 'required', newForm.error);
            if(state.forwardMailStartDate < state.stopForwardingMail){
                props.history.push(nextStep);
            } else {
                errors = {
                    ...errors,
                    stopForwardingMail: 'This date value not allowed to be before or same the stopping date!'
                }
                setErrorMsg(errors);
            }
        }

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`When should we start forwarding mail to your new address?`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <MDBRow center className={`mb-2`}>
                            <DatePicker
                                label="Date From?"
                                variant="inline"
                                id="dateFrom"
                                name={`dateFrom`}
                                disablePast={true}
                                format="dd/MM/yyyy"
                                error={!!errorMsg.forwardMailStartDate}
                                helperText={errorMsg.forwardMailStartDate}
                                value={state.forwardMailStartDate || null}
                                autoOk={true}
                                classes={{root: "custom-text-input"}}
                                placeholder={`Date from you want to receive Mails`}
                                onChange={e => onChange(e, 'forwardMailStartDate', e.toLocaleDateString())}
                                required
                            />
                        </MDBRow>

                        {(enableNext && state.moveType === "temp") && <React.Fragment>
                            <h4 className={`mb-2 text-center`}>When do you want to stop the Mails being forwarded to your Temporary Address?</h4>
                            <MDBRow center className={`mb-2`}>
                                <DatePicker
                                    label="Date To?"
                                    variant="inline"
                                    id="dateTo"
                                    name={`dateTo`}
                                    disablePast={true}
                                    format="dd/MM/yyyy"
                                    error={!!errorMsg.stopForwardingMail}
                                    helperText={errorMsg.stopForwardingMail}
                                    value={state.stopForwardingMail || null}
                                    autoOk={true}
                                    minDate={state.forwardMailStartDate || null}
                                    minDateMessage={`Stop Mail date should be after of start date!`}
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`Date from you want to stop receiving Mails`}
                                    onChange={e => onChange(e, 'stopForwardingMail', e.toLocaleDateString())}
                                    required
                                />
                            </MDBRow>
                        </React.Fragment>}

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}

export default Step4;
