import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import TextField from "@material-ui/core/TextField";
import {fieldValidator} from "./validator";
import CreditCardInput from 'react-credit-card-input';
import $ from "jquery";

const Step7 = props => {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});

    let errObj = {};
    let number, cvc, expiry;

    const cardDetails = state.cardDetails;

    const onChange = e => {
        let updatedState = {...state};
        let errors = {...errorMsg};

        let value = e.target.value;
        switch (e.target.id) {
            case 'card-number':
                updatedState.cardDetails['cardNumber'] = value;
                break;
            case 'name':
                updatedState.cardDetails['name'] = value;
                setErrorMsg(fieldValidator('name', updatedState.cardDetails, 'requiredChar', errors).error);
                break;
            case 'card-expiry':
                updatedState.cardDetails['cardExpiry'] = {
                    month: value.split('/')[0],
                    year: value.split('/')[1]
                };
                break;
            case 'cvc':
                updatedState.cardDetails['cvc'] = value;
                break;
            default:
                break;
        }
        if (e.target.id !== "name") {
            setErrorMsg({});
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        const cardNumber = $(`#number`);
        const expiry = $(`#expiry`);
        const cvc = $(`#cvc`);

        cardNumber.focus();
        cardNumber.blur();
        expiry.focus();
        expiry.blur();
        cvc.focus();
        cvc.blur();

        let errors = {...errorMsg, ...errObj};
        let newForm = {};

        newForm = fieldValidator('name', state.cardDetails, 'requiredChar', errors);

        console.log("Form: ", newForm);

        if (newForm.isValid) {
            // props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }


    };

    const handleError = e => {
        let errors = {...errObj};
        errors = {
            ...errors,
            [e.inputName]: e.error
        };
        console.log("Err: ", errors);
        errObj = {...errors};
    };

    return (
        <Step
            heading={`Please provide a credit card to verify your old address and for our change of address processing fee to be charged to.`}
        >
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <MDBRow center className={`mb-2`}>
                            <CreditCardInput
                                cardNumberInputProps={
                                    {
                                        value: cardDetails.cardNumber,
                                        id: `number`,
                                        onChange: onChange
                                    }
                                }
                                cardExpiryInputProps={
                                    {
                                        value: cardDetails.expiry,
                                        id: `expiry`,
                                        onChange: onChange
                                    }
                                }
                                cardCVCInputProps={
                                    {
                                        value: cardDetails.cvc,
                                        id: `cvc`,
                                        onChange: onChange
                                    }
                                }
                                fieldClassName="input"
                                onError={e => handleError(e)}
                            />
                        </MDBRow>
                        <MDBRow center className={`mb-2`}>
                            <TextField
                                error={!!errorMsg.name}
                                helperText={errorMsg.name}
                                label="Card Holder Name"
                                type="text"
                                id={`name`}
                                name="name"
                                value={state.cardDetails.name || ''}
                                placeholder={`Card Holder`}
                                classes={{root: "custom-text-input"}}
                                onChange={e => onChange(e, 'name', e.target.value)}
                                onBlur={e => onChange(e, 'name', e.target.value)}
                                required
                            />
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill Correct Details</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}

export default Step7;

