import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";

const Step3 = props => {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'moveType':
                updatedState.moveType = value;
                setErrorMsg(fieldValidator('moveType', updatedState, 'required', errors).error);
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {...errorMsg};
        let newForm = fieldValidator('moveType', state, 'required', errors);

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }

    };

    return (
        <Step
            heading={`What best describes you Move?`}
        >
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol md={`10`} lg={`8`}>
                        <MDBRow center>
                            <MDBFormInline className={`inline-components`}>
                                <MDBInput
                                    label="Permanent"
                                    type='checkbox'
                                    id='perm'
                                    name={`perm`}
                                    checked={state.moveType === "perm" || false}
                                    className={`custom-check-large`}
                                    containerClass='mr-5'
                                    onChange={e => onChange(e, "moveType", 'perm')}
                                />
                                <MDBInput
                                    label="Temporary"
                                    type='checkbox'
                                    id='temp'
                                    name={`temp`}
                                    checked={state.moveType === "temp" || false}
                                    className={`custom-check-large`}
                                    containerClass='mr-5'
                                    onChange={e => onChange(e, "moveType", 'temp')}
                                />
                            </MDBFormInline>
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}

export default Step3;
