import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBModal, MDBModalBody, MDBModalHeader, MDBRow} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import TermsOfService from "../TermsOfService";

const Step8 = props => {
    const {onChangeField, prevStep, nextStep, history} = props;
    const [showModal, setShowModal] = useState(false);


    const toggleModal = () => {
        setShowModal(!showModal);
    }

    return (
        <><Step
            heading={<>By submitting this application you understand that an application processing fee charged by SBA
                Disaster Pro's will be charged to the funding account entered above in the amount of $997 dollars to
                complete your application and approve this charge alongside our <a href="#" onClick={(e) => {
                    e.preventDefault();
                    toggleModal();
                }}>Terms of Service</a> please select I agree to decline SBA Disaster Pro's SBA service please select
                Decline</>
            }
        >
            <img
                src={BackBtn}
                className="btn-back"
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={10} md={8}>
                        <div className="my-4"/>
                        <MDBRow center>
                            <MDBCol md={6} lg={4}>
                                <MDBBtn
                                    color="pink"
                                    rounded
                                    type="button"
                                    className="btn-block z-depth-1a"
                                    onClick={(e) => {
                                        onChangeField(e, "agree", "I Agree");
                                        history.push(nextStep);
                                    }}
                                >
                                    I Agree
                                </MDBBtn>
                            </MDBCol>
                            <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                            <MDBCol md={6} lg={4}>
                                <MDBBtn
                                    color="pink"
                                    rounded
                                    type="button"
                                    className="btn-block z-depth-1a"
                                    onClick={(e) => {
                                        onChangeField(e, "agree", "I Decline");
                                        history.push("/decline");
                                    }}
                                >
                                    I Decline
                                </MDBBtn>
                            </MDBCol>
                        </MDBRow>
                        <br/>
                        <br/>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>

            <MDBModal size="fluid" isOpen={showModal} toggle={toggleModal}>
                <MDBModalHeader toggle={toggleModal}>Terms of Service</MDBModalHeader>
                <MDBModalBody>
                    <TermsOfService/>
                </MDBModalBody>
            </MDBModal>
        </>);
}
export default Step8;
