import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow} from "mdbreact";
import Step from "./Step";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";

const Step1 = props => {
    const {state, nextStep} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'fname':
            case 'lname':
                updatedState.userInfo[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.userInfo, 'requiredChar', errors).error);
                break;
        }
        props.onChangeState(updatedState);
    };

    const onNext = () => {
        let errors = {...errorMsg};
        let newForm;

        errors = fieldValidator('fname', state.userInfo, 'requiredChar', errors).error;
        newForm = fieldValidator('lname', state.userInfo, 'requiredChar', errors);

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step
            heading={
                "Hey! I'm Madison. I'll help you complete your change of address in seconds. Ready to go?"
            }
        >
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    error={!!errorMsg.fname}
                                    helperText={errorMsg.fname}
                                    label="First Name"
                                    type="text"
                                    id={`fname`}
                                    name="fname"
                                    value={state.userInfo.fname || ''}
                                    placeholder={`Your First Name`}
                                    classes={{root: "custom-text-input"}}
                                    onChange={e => onChange(e, 'fname', e.target.value)}
                                    onBlur={e => onChange(e, 'fname', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    error={!!errorMsg.lname}
                                    helperText={errorMsg.lname}
                                    label="Last Name"
                                    type="text"
                                    id={`lname`}
                                    name="lname"
                                    value={state.userInfo.lname || ''}
                                    placeholder={`Your Last Name`}
                                    classes={{root: "custom-text-input"}}
                                    onChange={e => onChange(e, 'lname', e.target.value)}
                                    onBlur={e => onChange(e, 'lname', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {(errorMsg.msg) && (
                                <div className="text-danger mb-2">{errorMsg.msg}</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Let's Do This
                            </MDBBtn>
                        </MDBRow>

                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
export default Step1;

