import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";

const Step6 = props => {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'phnNumber':
                updatedState[fieldName] = value;
                if (errors.phnNumber) {
                    setErrorMsg(fieldValidator(fieldName, updatedState, 'phone', errors).error);
                }
                break;
            case 'email':
                updatedState[fieldName] = value;
                if (errors.email) {
                    setErrorMsg(fieldValidator(fieldName, updatedState, 'email', errors).error);
                }
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {...errorMsg};
        let newForm;

        errors = fieldValidator('phnNumber', state, 'phone', errors).error;
        newForm = fieldValidator('email', state, 'email', errors);

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`Contact Information`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <h4 className={`mb-2 text-center`}>
                            What's the best phone number to reach you at if we have any questions?
                        </h4>
                        <MDBRow center className={`mb-2`}>
                            <TextField
                                error={!!errorMsg.phnNumber}
                                helperText={errorMsg.phnNumber}
                                label="Phone Number"
                                type="number"
                                id={`phnNumber`}
                                name="phnNumber"
                                value={state.phnNumber || ''}
                                placeholder={`Your Phone Name`}
                                classes={{root: "custom-text-input"}}
                                onChange={e => onChange(e, 'phnNumber', e.target.value)}
                                onBlur={e => onChange(e, 'phnNumber', e.target.value)}
                                required
                            />
                        </MDBRow>

                        <h4 className={`mb-2 text-center`}>
                            What's the best email address for us to email you confirmation of your change of address?
                        </h4>
                        <MDBRow center className={`mb-2`}>
                            <TextField
                                error={!!errorMsg.email}
                                helperText={errorMsg.email}
                                label="Email ID"
                                type="text"
                                id={`email`}
                                name="email"
                                value={state.email || ''}
                                placeholder={`Your Email Address`}
                                classes={{root: "custom-text-input"}}
                                onChange={e => onChange(e, 'email', e.target.value)}
                                onBlur={e => onChange(e, 'email', e.target.value)}
                                required
                            />
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}

export default Step6;
